class MfilegroupEditComponent extends MfilegroupComponent {
    constructor(mfilegroupService, injector, router, route, location) {
        super(mfilegroupService, injector, router, route, location);
        this.mfilegroupService = mfilegroupService;
        this.injector = injector;
        this.router = router;
        this.route = route;
        this.location = location;
        this.done = new EventEmitter();
        this.doneData = new EventEmitter();
        this.minDate = { year: (new Date()).getFullYear() - 100, month: 1, day: 1 };
        this.view = ViewType.EDIT;
        this.stringFields.push('name');
        const detail = {};
        this.detail = this.formatDetail(detail);
    }
    ngOnInit() {
        if (this.embedMode == 'create') { // parent ask to create
            this.action = 'Create';
            this.getDetailData();
        }
        else {
            if (!this.id) {
                this.id = this.route.snapshot.paramMap.get('id');
            }
            if (this.id) {
                this.action = 'Edit';
                this.populateDetailForAction(this.id, 'edit'); // populate with action as "edit"
            }
            else {
                this.action = 'Create';
                if (!this.cid) {
                    this.cid = this.route.snapshot.queryParamMap.get('cid');
                }
                if (this.cid) {
                    this.populateDetailFromCopy(this.cid);
                }
                else {
                    this.getDetailData();
                }
            }
        }
    }
    getDetailData() {
        if (this.initData) {
            this.action = 'Add';
            const detail = {};
            for (const prop of Object.keys(this.initData)) {
                detail[prop] = this.initData[prop];
                this.hiddenFields.push(prop);
            }
            this.detail = this.formatDetail(detail);
        }
        else {
            const detail = {};
            this.detail = this.formatDetail(detail);
        }
    }
}
